
.Perfil td{
	float: left;
}
.Imagen{
	width: 400px;
}

.Imagen img{
	margin: 50px;
	width: 250px;
    height: auto;
    -ms-flex-line-pack: center;
        align-content: center;
    border: 2px solid #f2f2f2;
    border-radius: 50%;
    -webkit-transition-duration: 0.4s;
         -o-transition-duration: 0.4s;
            transition-duration: 0.4s;
}

.Imagen img:hover{
    border-radius: 15%;
}

.Info{
	padding: 30px;
	width: 300px;
}
.Titulo{
	text-align: center;
	padding: 25px;
}


